import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-event/@core/permissions/permission.store';
import { DataStoreService } from './pics-event/@core/service/data-store.service';
import { RBACINFO } from './pics-event/@core/urls/event-scheduler-url.config';

@Component({
  selector: 'event',
  template: `
        <event-scheduler></event-scheduler>
  `,
  styles: [
  ]
})
export class EventComponent implements OnInit {


  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public schedulerEvent!: Observable<any>;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {
  }
  ngOnInit() {
     this.schedulerEvent.subscribe((val: any) => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
    })
  }
}
