

import { AbstractControl, ValidationErrors, ValidatorFn } from '@angular/forms';
import { AppConstants } from './app-constants';


export class RegexConstants {
  public static URLVALIDATE = new RegExp(AppConstants.EventURLValidate);
}
export class RegexUrlConstants {
  public static URLVALIDATE = AppConstants.URLVALIDATE;
}
export class RegexUrlMultipleConstants {
  public static URLVALIDATE = new RegExp(AppConstants.regexEmailType2);
}

export function RegexValidator(reg: RegexConstants): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (control.value && !control.value.toString().match(reg)) {
      return { urlvalidate: true };
    }
    return null;
  };
}
export function CommaSepEmail(_reg: any): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (control.value) {
      const emails = control.value.split(',');
      const forbidden = emails.some((email: any) => {
        if (email) {
          return !RegexUrlMultipleConstants.URLVALIDATE.test(email);
        } else {
          return null;
        }
      });
      return forbidden ? { externalUrl: true } : null;
    }
    return null;
  };
}
